.timecontainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.time {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  border-bottom: 1px solid #d3d3d3;
  cursor: pointer;
}

.time input {
  cursor: pointer;
}

.buttonarea {
  text-align: center;
  background-color: white;
  height: 56px;
  padding: 45px 0px;
  position: fixed;
  bottom: 0;
  left: 16px;
  right: 16px;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 10px;
  width: 100%;
  height: 56px;
  border-radius: 10px;
  border: none;
  background-color: #f0f0f1;
  color: #93949c;
}

.buttonActive {
  background-color: #262737;
}

.button span {
  color: inherit; /* Поддержка изменения цвета текста */
}
